import React, { useState } from "react";
import { SelectricWrap } from "../../hoc/SelectricWrap";
import { TaskItem } from "../../components/Task";
import { TaskPopupAdd } from "../../components/TaskPopupAdd";
import { ContentHeader } from "../../components/ContentHeader";

import "./styles.css";

export const Tasks = SelectricWrap((props) => {
  const { children } = props;

  const [close, setClose] = useState(true);

  const options = {
    title: "",
    optionValues: [
      [
        " ",
        " ",
        " ",
        " ",
        " ",
      ],
    ],
    buttonText: " ",
    handleClick: () => {
      setClose(false);
    },
  };

  return (
    <>
      {!close && <TaskPopupAdd setClose={setClose}></TaskPopupAdd>}
      {children}
      <div className="content">
        <div className="content-inner">
          <div className="content-row">
            <ContentHeader {...options}></ContentHeader>
            <div className="task">
              <div className="task-container">
                <div className="task-group">
                  <div className="task-title">
                    <span></span>
                  </div>
                  <div className="task-list">
                    <TaskItem
                      name=" "
                      source="   "
                      count="3"
                    ></TaskItem>
                    <TaskItem
                      name=" "
                      source="   "
                      count="1"
                    ></TaskItem>
                    <TaskItem
                      name=" "
                      source="   "
                      count="2"
                    ></TaskItem>
                  </div>
                </div>
                <div className="task-group">
                  <div className="task-title">
                    <span></span>
                  </div>
                  <div className="task-list">
                    <div className="task-placeholder">
                      <span>  </span>
                    </div>
                  </div>
                </div>
                <div className="task-group">
                  <div className="task-title">
                    <span></span>
                  </div>
                  <div className="task-list">
                    <TaskItem
                      name=" "
                      source="   "
                      count="1"
                    ></TaskItem>
                    <TaskItem
                      name=" "
                      source="   "
                      count="2"
                    ></TaskItem>
                  </div>
                </div>
                <div className="task-group">
                  <div className="task-title">
                    <span></span>
                  </div>
                  <div className="task-list">
                    <div className="task-placeholder">
                      <span>  </span>
                    </div>
                  </div>
                </div>
                <div className="task-group">
                  <div className="task-title">
                    <span></span>
                  </div>
                  <div className="task-list">
                    <div className="task-placeholder">
                      <span>  </span>
                    </div>
                  </div>
                </div>
                <div className="task-group">
                  <div className="task-title">
                    <span></span>
                  </div>
                  <div className="task-list">
                    <div className="task-placeholder">
                      <span>  </span>
                    </div>
                  </div>
                </div>
                <div className="task-group">
                  <div className="task-title">
                    <span></span>
                  </div>
                  <div className="task-list">
                    <div className="task-placeholder">
                      <span>  </span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
});
