import React, { useState, useEffect } from "react";

import * as Yup from "yup";
import { inject } from "mobx-react";
import { Formik, Form } from "formik";
import { Link, useHistory } from "react-router-dom";

import { AuthInput } from "@shared/AuthInput";
import { AuthButton } from "@shared/AuthButton";

import image from "@images/logo-auth.svg";

const fieldData = [
  {
    label: "E-mail*",
    fieldType: "email",
    placeholder: "example@mail.ru",
  },
  {
    label: "*",
    fieldType: "password",
    type: "password",
    placeholder: "*****",
  },
];

export const Login = inject("authStore")((props) => {
  const { authenticateUser } = props.authStore;
  const history = useHistory();

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);

  useEffect(() => {
    const id = localStorage.getItem("id");

    if (id) {
      history.push("/registration");
    }
  });

  const AuthInputs = fieldData.map(
    ({ label, fieldType, type, placeholder }) => (
      <AuthInput
        label={label}
        fieldType={fieldType}
        type={type}
        placeholder={placeholder}
      ></AuthInput>
    )
  );

  return (
    <Formik
      initialValues={{ email: "", password: "" }}
      validationSchema={Yup.object({
        email: Yup.string()
          .email("   E-mail")
          .required(" "),
        password: Yup.string()
          .min(2, "   2   ")
          .required(" "),
      })}
      onSubmit={async (values, { setSubmitting }) => {
        const { email, password } = values;

        try {
          setLoading(true);
          await authenticateUser({ email, password });
          setLoading(false);

          history.push("/");
        } catch (e) {
          setLoading(false);
          setError(true);
        }
      }}
    >
      <Form>
        <div className="auth">
          <div className="auth-modal">
            <div className="auth-logo">
              <img src={image} />
            </div>
            <div className="auth-container">
              <div className="auth-title">
                <span></span>
              </div>
              <div className="auth-row">{AuthInputs}</div>
              <div className="auth-row">
                <div className="auth-string">
                  <div className="auth-button">
                    <AuthButton loading={loading} value=""></AuthButton>
                  </div>
                </div>
              </div>
              {error && (
                <div className="auth-row">
                  <div className="auth-string">
                    <div className="auth-error">
                      <span>{" "}</span>
                    </div>
                  </div>
                </div>
              )}
            </div>
            <div className="auth-footer">
              <div className="auth-items">
                <div className="auth-item">
                  <Link to="/recovery/password" className="auth-link">
                    <span> ?</span>
                  </Link>
                </div>
                <div className="auth-item">
                  <Link to="/registration" className="auth-link">
                    <span></span>
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Form>
    </Formik>
  );
});
