import React, { useState } from "react";

import image from "@images/no-avatar.png";
import * as classNamenames from "classnames";

export const TaskItem = (props) => {
  const { name, source, count } = props;
  const [checked, setChecked] = useState(props.checked);

  const Creators = Array.from({ length: +count }, () => {
    return (
      <a
        href="#"
        className="task-creator-item tooltip-hint-bottom"
        data-hint=" "
      >
        <div className="task-creator-avatar">
          <img src={image} />
        </div>
      </a>
    );
  });

  const handleChange = () => {
    setChecked(!checked);
  };

  const classNameNames = classNamenames("tast-list-item", {
    "task-checked": checked,
  });

  return (
    <div className={classNameNames}>
      <div className="task-body">
        <label className="checkbox">
          <input
            className="checkbox-input"
            type="checkbox"
            onChange={handleChange}
          />
          <div className="checkbox-button"></div>
        </label>
        <div className="task-name">
          <a href="#">
            <span>{name}</span>
          </a>
        </div>
        <div className="task-source">
          <a href="#">
            <span>{source} </span>
          </a>
        </div>
        <div className="task-creator">
          <div className="task-creator-items">{Creators}</div>
        </div>
      </div>
    </div>
  );
};
