import React from "react";
import { inject, observer } from "mobx-react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import { UnauthenticatedRoutes } from "@routes/UnauthenticatedRoutes";
import { AuthenticatedRoutes } from "@routes/AuthenticatedRoutes";

import { history } from "@utils/history";
import { EmailVerification } from "@scenes/EmailVerification";

export const AppRouter = inject("authStore")(
  observer((props) => {
    const { authenticated } = props.authStore.user;

    return (
      <Router basename="/testingcrm" history={history}>
        <Switch>
          <Route
            exact
            path="/email/verification/:token"
            component={EmailVerification}
          />
          {authenticated ? <AuthenticatedRoutes /> : <UnauthenticatedRoutes />}
        </Switch>
      </Router>
    );
  })
);
