import * as Yup from "yup";
import { inject } from "mobx-react";
import { Formik, Form } from "formik";
import { useHistory } from "react-router-dom";
import React, { useEffect, useState } from "react";

import { AuthInput } from "@shared/AuthInput";
import { AuthButton } from "@shared/AuthButton";

import image from "@images/logo-auth.svg";

import "./styles.css";

const fieldData = [
  {
    label: "",
    fieldType: "name",
    placeholder: "",
  },
  {
    label: " ",
    fieldType: "company",
    placeholder: "",
  },
  {
    label: " ",
    fieldType: "phoneNumber",
    placeholder: "+7 123 456 789",
  },
];

export const RegistrationComplete = inject("authStore")((props) => {
  const { completeRegistration } = props.authStore;
  const history = useHistory();

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);

  useEffect(() => {
    const id = localStorage.getItem("id");

    if (!id) {
      history.push("registration");
    }
  });

  const AuthInputs = fieldData.map(({ label, fieldType, placeholder }) => (
    <AuthInput
      label={label}
      fieldType={fieldType}
      placeholder={placeholder}
    ></AuthInput>
  ));

  return (
    <Formik
      initialValues={{ name: "", company: "", phoneNumber: "" }}
      validationSchema={Yup.object({
        name: Yup.string().required(" "),
        company: Yup.string().required(" "),
        phoneNumber: Yup.string().required(" "),
      })}
      onSubmit={async (values, { setSubmitting }) => {
        const { name, company, phoneNumber } = values;

        try {
          setLoading(true);
          const id = localStorage.getItem("id");
          await completeRegistration(id, {
            name,
            company,
            phoneNumber,
          });
          setLoading(false);

          history.push("/");
        } catch (e) {
          setLoading(false);
          setError(true);
        }
      }}
    >
      <Form>
        <div className="auth">
          <div className="auth-modal">
            <div className="auth-logo">
              <img src={image} />
            </div>
            <div className="auth-container">
              <div className="auth-title">
                <span></span>
              </div>
              <div className="auth-row">{AuthInputs}</div>
              <div className="auth-row">
                <div className="auth-string">
                  <div className="auth-button">
                    <AuthButton
                      loading={loading}
                      value=""
                    ></AuthButton>
                  </div>
                </div>
              </div>
              {error && (
                <div className="auth-row">
                  <div className="auth-string">
                    <div className="auth-error">
                      <span> </span>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </Form>
    </Formik>
  );
});
