import { action, makeObservable, observable } from "mobx";

import { StatusCode } from "../../consts";
import { postRequest } from "@utils/request";

export class AuthStore {
  constructor() {
    this.user = {
      authenticated: false || localStorage.getItem("token"),
    };

    this.logOut = this.logOut.bind(this);
    this.authenticateUser = this.authenticateUser.bind(this);
    this.completeRegistration = this.completeRegistration.bind(this);

    makeObservable(this, {
      user: observable,
      completeRegistration: action,
    });
  }

  async registrateUser(data) {
    try {
      const { status, id } = await postRequest("users/insert", data);
      if (status === StatusCode.OK) {
        return id;
      } else {
        throw new Error("User exists!");
      }
    } catch (e) {
      throw e;
    }
  }

  async completeRegistration(id, data) {
    try {
      const { token, status } = await postRequest(
        "users/complete_auth/" + id,
        data
      );

      if (status === StatusCode.OK) {
        this.user.authenticated = true;
        localStorage.setItem("id", "");
        localStorage.setItem("token", token);
      } else {
        throw status;
      }

      return "success";
    } catch (e) {
      throw e;
    }
  }

  async authenticateUser(data) {
    try {
      const { token, status } = await postRequest("users/login", data);

      if (status === StatusCode.OK) {
        localStorage.setItem("token", token);
        this.user.authenticated = true;
      } else {
        throw new Error("Wrong password or email!");
      }
    } catch (e) {
      throw e;
    }
  }

  getPassCode(data) {
    postRequest("users/get_pass_code", data);
  }

  changePassword(data) {
    postRequest("users/change_pass", data);
  }

  async verifyEmail(token) {
    try {
      const status = await postRequest("users/verify_email", { token });

      return status;
    } catch (err) {
      throw err;
    }
  }

  logOut(history) {
    this.user.authenticated = false;
    localStorage.clear();
    history.push("/login");
  }
}
