import React, { useEffect } from "react";
import { useParams, useHistory } from "react-router-dom";
import { inject } from "mobx-react";

import image from "@images/logo-auth.svg";

export const EmailVerification = inject("authStore")((props) => {
  const { verifyEmail } = props.authStore;

  const { token } = useParams();
  const history = useHistory();

  useEffect(() => {
    const accept = async () => {
      try {
        const { status: responseStatus } = await verifyEmail(token);

        if (responseStatus !== 200) throw new Error("Redirect!");
      } catch (e) {
        history.push("/");
      }
    };

    accept();
  });

  const handleClick = () => {
    history.push("/");
  };

  return (
    <div class="auth">
      <div class="auth-modal">
        <div class="auth-logo">
          <img src={image} />
        </div>
        <div class="auth-container">
          <div class="auth-title">
            <span> </span>
          </div>
          <div class="auth-row">
            <div class="auth-string">
              <div class="auth-description">
                <p>   .</p>
              </div>
            </div>
          </div>
          <div class="auth-row">
            <div class="auth-string">
              <div class="auth-button">
                <span class="button" onClick={handleClick}>
                   
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
});
