import React from "react";
import { Switch, Route, useRouteMatch } from "react-router-dom";

import { RecoveryPasswordMain } from "./components/Main";
import { RecoveryPasswordReset } from "./components/Reset";

export const RecoveryPassword = () => {
  const { path } = useRouteMatch();

  return (
    <Switch>
      <Route exact path={`${path}/password`} component={RecoveryPasswordMain} />
      <Route
        path={`${path}/password/:token`}
        component={RecoveryPasswordReset}
      />
    </Switch>
  );
};
