import React, { useState } from "react";

export const ModalClient = (props) => {
  const { setVisible, setTaskSource } = props;
  const [source, setSource] = useState("");

  const handleChange = (event, sourceName) => {
    const { value } = event.target;

    if (value === "on") {
      setSource(sourceName);
    } else {
      setSource(null);
    }
  };

  const handleClick = () => {
    setTaskSource(source);
    setVisible(false);
  };

  return (
    <div className="modal">
      <div className="modal-add-wrapper">
        <div className="modal-container">
          <button className="close" onClick={() => setVisible(false)}></button>
          <div className="modal-inner-content">
            <div className="modal-title">
              <span> </span>
            </div>
            <div className="modal-row">
              <div className="modal-label">
                <span></span>
              </div>
              <div className="modal-input">
                <input type="text" />
              </div>
            </div>
            <div className="modal-row">
              <div className="staff-items">
                <div className="staff-item">
                  <div className="staff-container">
                    <div className="staff-inner-items">
                      <div className="staff-inner-content">
                        <div className="staff-name">
                          <span> </span>
                        </div>
                      </div>
                      <div className="staff-inner-checkbox">
                        <label className="radio">
                          <input
                            className="radio-input"
                            type="radio"
                            onChange={(e) => handleChange(e, "Mustafo Faiz")}
                          />
                          <div className="radio-button"></div>
                        </label>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="modal-button">
              <button className="button" onClick={handleClick}>
                <span></span>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
