import * as Yup from "yup";
import { inject } from "mobx-react";
import { Formik, Form } from "formik";
import React, { useEffect, useState } from "react";
import { Link, useHistory } from "react-router-dom";

import { AuthInput } from "@shared/AuthInput";
import { AuthButton } from "@shared/AuthButton";

import logoImage from "@images/logo-auth.svg";

const fieldData = [
  {
    label: "E-mail",
    fieldType: "email",
    placeholder: "example@mail.ru",
  },
  {
    label: "",
    fieldType: "password",
    placeholder: "*****",
    type: "password",
  },
];

export const RegistrationMain = inject("authStore")((props) => {
  const { registrateUser } = props.authStore;
  const history = useHistory();

  const [error, setError] = useState(false);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const id = localStorage.getItem("id");

    if (id) {
      history.push("/registration/complete");
    }
  });

  const AuthInputs = fieldData.map(
    ({ label, fieldType, placeholder, type }) => (
      <AuthInput
        type={type}
        label={label}
        fieldType={fieldType}
        placeholder={placeholder}
      ></AuthInput>
    )
  );

  return (
    <Formik
      initialValues={{ email: "", password: "" }}
      validationSchema={Yup.object({
        email: Yup.string()
          .email("   E-mail")
          .required(" "),
        password: Yup.string()
          .min(2, "   2   ")
          .required(" "),
      })}
      onSubmit={async (values, { setSubmitting }) => {
        const { email, password } = values;

        try {
          setLoading(true);
          const id = await registrateUser({ email, password });
          setLoading(false);

          localStorage.setItem("id", id);
          history.push("/registration/complete");
        } catch (e) {
          setLoading(false);
          setError(true);
        }
      }}
    >
      <Form>
        <div className="auth">
          <div className="auth-modal">
            <div className="auth-logo">
              <img src={logoImage} />
            </div>
            <div className="auth-container">
              <div className="auth-title">
                <span></span>
              </div>
              <div className="auth-row">{AuthInputs}</div>
              <div className="auth-row">
                <div className="auth-string">
                  <div className="auth-button">
                    <AuthButton
                      loading={loading}
                      value=""
                    ></AuthButton>
                  </div>
                </div>
              </div>
              {error && (
                <div className="auth-row">
                  <div className="auth-string">
                    <div className="auth-error">
                      <span> </span>
                    </div>
                  </div>
                </div>
              )}
            </div>
            <div className="auth-footer">
              <div className="auth-items">
                <div className="auth-item">
                  <Link to="/login" className="auth-link">
                    <span></span>
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Form>
    </Formik>
  );
});