import * as Yup from "yup";
import { inject } from "mobx-react";
import { Formik, Form } from "formik";
import React, { useState } from "react";
import { Link, useHistory } from "react-router-dom";

import { AuthInput } from "@shared/AuthInput";
import { AuthButton } from "@shared/AuthButton";

import image from "@images/logo-auth.svg";

export const RecoveryPasswordMain = inject("authStore")((props) => {
  const history = useHistory();
  const { getPassCode } = props.authStore;

  const [sended, setSended] = useState(false);
  const [loading, setLoading] = useState(false);

  return (
    <Formik
      initialValues={{ email: "", password: "" }}
      validationSchema={Yup.object({
        email: Yup.string()
          .email("   E-mail")
          .required(" "),
      })}
      onSubmit={async (values, { setSubmitting }) => {
        if (!sended) {
          const { email } = values;

          try {
            setLoading(true);
            await getPassCode({ email });
            setLoading(false);
            setSended(true);

            setTimeout(() => {
              history.push("/");
            }, 2000);
          } catch (e) {
            setLoading(false);
          }
        }
      }}
    >
      <Form>
        <div className="auth">
          <div className="auth-modal">
            <div className="auth-logo">
              <img src={image} />
            </div>
            <div className="auth-container">
              <div className="auth-title">
                <span> </span>
              </div>
              <div className="auth-row">
                <AuthInput
                  label="E-mail"
                  fieldType="email"
                  placeholder="example@mail.ru"
                ></AuthInput>
              </div>
              <div className="auth-row">
                <div className="auth-string">
                  <div className="auth-button">
                    <AuthButton
                      value={sended ? "  " : ""}
                      loading={loading}
                    ></AuthButton>
                  </div>
                </div>
              </div>
            </div>
            <div className="auth-footer">
              <div className="auth-items">
                <div className="auth-item">
                  <Link to="/login" className="auth-link">
                    <span></span>
                  </Link>
                </div>
                <div className="auth-item">
                  <Link to="/registration" className="auth-link">
                    <span></span>
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Form>
    </Formik>
  );
});
