import React from "react";

import loader from "@images/loader.gif";

export const AuthButton = (props) => {
  const { loading, value } = props;

  return (
    <>
      {loading ? (
        <center>
          <img width="30px" src={loader} alt="" />
        </center>
      ) : (
        <button type="submit" className="button">
          {value}
        </button>
      )}
    </>
  );
};
