import * as Yup from "yup";
import { inject } from "mobx-react";
import { Formik, Form } from "formik";
import React, { useState } from "react";
import { useHistory, useParams } from "react-router-dom";

import { AuthInput } from "@shared/AuthInput";
import { AuthButton } from "@shared/AuthButton";

import image from "@images/logo-auth.svg";

const fieldData = [
  {
    label: " ",
    fieldType: "password",
    placeholder: "*****",
    type: "password",
  },
  {
    label: " ",
    fieldType: "passwordTry",
    placeholder: "*****",
    type: "password",
  },
];

export const RecoveryPasswordReset = inject("authStore")((props) => {
  const { changePassword } = props.authStore;

  const { token } = useParams();
  const history = useHistory();

  const [error, setError] = useState(false);
  const [sended, setSended] = useState(false);
  const [loading, setLoading] = useState(false);

  const AuthInputs = fieldData.map(
    ({ label, fieldType, placeholder, type }) => (
      <AuthInput
        label={label}
        fieldType={fieldType}
        placeholder={placeholder}
        type={type}
      ></AuthInput>
    )
  );

  return (
    <Formik
      initialValues={{ password: "", passwordTry: "" }}
      validationSchema={Yup.object({
        password: Yup.string().required(" "),
        passwordTry: Yup.string().required(" "),
      })}
      onSubmit={async (values, { setSubmitting }) => {
        if (!sended) {
          const { password, passwordTry } = values;

          if (password !== passwordTry) {
            setError(true);
          } else {
            setError(false);

            try {
              setLoading(true);
              await changePassword({ newPass: password, code: token });
              setLoading(false);
              setSended(true);

              setTimeout(() => {
                history.push("/");
              }, 2000);
            } catch (e) {
              setLoading(false);
            }
          }
        }
      }}
    >
      <Form>
        <div className="auth">
          <div className="auth-modal">
            <div className="auth-logo">
              <img src={image} />
            </div>
            <div className="auth-container">
              <div className="auth-title">
                <span></span>
              </div>
              <div className="auth-row">{AuthInputs}</div>
              <div className="auth-row">
                <div className="auth-string">
                  <div className="auth-button">
                    <AuthButton
                      loading={loading}
                      value={!sended ? " " : ""}
                    ></AuthButton>
                  </div>
                </div>
              </div>
              {error && (
                <div className="auth-row">
                  <div className="auth-string">
                    <div className="auth-error">
                      <span>  </span>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </Form>
    </Formik>
  );
});
