import React, { useState } from "react";

export const DealTableEdit = (props) => {
  const { handleClose } = props;
  const [dealTableName, setDealTableName] = useState("");

  return (
    <div className="modal">
      <div className="modal-employee-wrapper">
        <div className="modal-container">
          <button
            className="close"
            onClick={() => handleClose("editTable")}
          ></button>
          <div className="modal-inner-content">
            <div className="modal-title">
              <span> </span>
            </div>
            <div className="modal-row">
              <div className="modal-label">
                <span></span>
              </div>
              <div className="modal-input">
                <input
                  type="text"
                  onChange={(e) => setDealTableName(e.target.value)}
                />
              </div>
            </div>
            <div className="modal-button">
              <div className="modal-button-items">
                <div className="modal-button-item">
                  <button className="button">
                    <span> </span>
                  </button>
                </div>
                <div className="modal-button-item">
                  <button className="button button-delete">
                    <span></span>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
